function validate() {
    let result = true;
    const modules = items.value.filter(e => e.type == "module" && e.anyChildChecked);
    const appLayerItems = items.value.filter(e => e.type == "qjson" && e.checked);

    if (props.screenType == "Deploy") {
        //FIXME: externalModules will be get from common places 
        if (modules.length > 0)
            result = modules
                .filter(e => e.name != "processmanager").map(e => e.children).filter(e => !e?.find(f => f.type == "entityDesigner" && f.checked)).length <= 0

        let isApplicationWillDeploy = appLayerItems.length > 0;
        if (isApplicationWillDeploy) {
            result = appLayerItems
                .map(e => viewModel.studio.items.find(item => item.ID == e.id) as IModel)
                .findIndex(e => ((e.additionals as IQJsonAdditionals).isRootPage)) != -1;
        }
    }

    return result;
}

function mobileFilter (model: IModel) {
    return props.platformSelection.web || !props.platformSelection.mobile ||
         !(model.modelType == "bpmn" || model.modelType == "entityDesigner" || model.modelType == "process");
} 

function handleItems() {
    items.value = viewModel.studio.items
        .filter((item) => item.objectType == "module" && item.children.length > 0)
        .map(
            (item) =>
            ({
                id: item.ID,
                collapsed: true,
                name: item.name,
                type: "module",
                anyChildChecked: true,
                allChildChecked: true,
                children: (item as IModule).children
                    .filter((child) => child.objectType == "model")
                    .filter(e => !props || props.screenType == "Export" || mobileFilter(e as IModel))
                    .map(
                        (child) =>
                        ({
                            id: child.ID,
                            parentId: item.ID,
                            checked: true,
                            name: child.name,
                            type: (child as IModel).modelType,
                            version: "latest",
                        } as ITableData)
                    ),
            } as ITableData)
        );
    viewModel.studio.items
        .filter((item) => item.objectType == "model" && item.usageType != "appSettings")
        .forEach((item) => {
            items.value.push({
                id: item.ID,
                checked: true,
                name: item.name,
                type: (item as IModel).modelType,
                version: "latest",
            });
        });
    const uiSettingsID = CryptoHelper.CreateGuid();
    const appSettings = viewModel.studio.items.filter((item) => item.objectType == "model" && item.usageType == "appSettings") as Array<IModel>;
    const uiSettings: ITableData = {
        id: uiSettingsID,
        collapsed: true,
        name: "UI Settings",
        type: "module",
        anyChildChecked: true,
        allChildChecked: true,
        children: appSettings.map((setting) => ({
            id: setting.ID,
            parentId: uiSettingsID,
            name: setting.name,
            type: (setting as IModel).modelType,
            checked: false,
            usageType: setting?.usageType,
        })) as Array<ITableData>,
    };
    items.value.unshift(uiSettings);
}

function handleModuleCountCheckboxEvent(value: boolean) {
    items.value.forEach((item) => {
        item.checked = value;
        if (item.allChildChecked != undefined) item.allChildChecked = value;
        if (item.anyChildChecked != undefined) item.anyChildChecked = value;
        if (item.children) item.children.forEach((child) => (child.checked = value));
    });
}

function handleCollapse(item: ITableData) {
    item.collapsed = !item.collapsed;
}

function handleItemCheckEvent(item: ITableData) {
    if (item.type != "module") {
        item.checked = !item.checked;
        if (item.parentId) {
            const parent = items.value.find((i) => i.id == item.parentId)!;
            handleParentCheckedStatus(parent);
        }
    } else {
        if(item.children && item.children.length > 0) {
            if (item.anyChildChecked) {
                item.children?.forEach((child) => (child.checked = false));
                item.checked = false;
            } else {
                item.children?.forEach((child) => (child.checked = true));
                item.checked = true;
            }
            handleParentCheckedStatus(item);
        }
        else {
            item.checked = !item.checked;
            item.allChildChecked = false;
        }
        
    }
}

function handleParentCheckedStatus(item: ITableData) {
    item.allChildChecked = !item.children?.some((child) => !child.checked);
    item.anyChildChecked = item.children?.some((child) => child.checked);
}

function syncPublishTargets(){
    publishTargets.value = []; disabledOptions.value= [];

    const targets = viewModel.user?.selectedOrganization?.actions?.publish?.targets;
    if(!targets)
        return;

    const isSelectAnyItem = items.value.find(e => e.anyChildChecked || e.checked);
    if(!isSelectAnyItem){
        publishTargets.value = Object.entries(targets).map(e => cloudTargetMap(e[0])!);
        const disabledOptionList = Array<IDisabledOptions>();
        for(let i=0; i<publishTargets.value.length; i++){
            disabledOptionList.push({index:i, cause: "You need to include items to publish."})
        }
        disabledOptions.value = disabledOptionList;
        return;
    }

    const checkedPublishTargets = checkAndSetDisabledCauses(targets);
    const sortedTargets = checkedPublishTargets.sort((e, f) => ( !e.cause === !f.cause) ? 0 : e.cause ? 1: -1);

    const disabledOptionList = Array<IDisabledOptions>();
    for(let i=0; i<sortedTargets.length; i++){
        const cause = sortedTargets[i].cause;
        if(cause) disabledOptionList.push({index:i, cause})
    }
    disabledOptions.value = disabledOptionList;
    publishTargets.value = sortedTargets.map(e => e.optionName);

    selectedTarget.value = !sortedTargets[0].cause ? sortedTargets[0].optionName : "";
}

function checkAndSetDisabledCauses(targets: Record<keyof IOrganizationCloud, boolean>){
    const list = Array<{optionName: string, cause?: string}>();

    Object.keys(targets).forEach(e => {
        const key = e as keyof IOrganizationCloud;
        const value = targets[key];
        const optionName = cloudTargetMap(key)!;
        if(!value)
            return list.push({optionName, cause:  "You are not authorized to publish to this target." });
        

        switch (key) {
            case "azure":
                const traverseSelectedItems = (status: boolean, items?: Array<ITableData>) => {
                    if (!items || items.length == 0) { return status; }
                    for (const item of items) {
                        if(item.checked || item.anyChildChecked){
                            if (item.type == "module") {
                                status = traverseSelectedItems(status, item.children);
                            }
                            if(item.type == "entityDesigner" || item.type == "bpmn" || item.type == "process"){
                                status = false;
                                break;
                            }  
                        }
                        
                    }
                    return status;
                }

                const status = traverseSelectedItems(true, items.value);
                if(!status)
                    return list.push({optionName, cause: "Entity and process models cannot be published to this target." });
                break;
            case "plateauGCP":
                if(props.platformSelection.mobile)
                    return list.push({optionName, cause: "This target is not available for mobile publishing." });
                break;
        }

        list.push({optionName});
    })

    return list;
}

function versionDialog(item: ITableData) {
    const clickedNode = { id: item.id, state: { checked: item.checked }, text: item.name, type: item.type, version: item.version };
    dialog.showDialog(
        defineAsyncComponent(() => import("../../application/modelVersionDialog.vue")),
        { closable: true, title: "Change Version", width: "900px", maxHeight: "500px", overflow: "auto" },
        { clickedNode }
    );
}

function close() {
    emit("close");
}

async function save() {
    executor.execute(
        async () => {
            const initAsGroup = async () => {
                const groupID = CryptoHelper.CreateGuid();
                await initHistory(groupID);
                await packJob(groupID);
                return groupID;
            }

            switch (props.screenType) {
                case "Export":
                    const groupID = await initAsGroup();
                    await createExportJob(groupID);
                    break;
                case "Deploy":
                    if(props.platformSelection.mobile)
                        return await createAzureDeploy();
                    debugger;
                    switch (cloudTargetMap(selectedTarget.value!, true) as keyof IOrganizationCloud) {
                        case "plateauGCP":
                            const groupID = await initAsGroup();
                            await createDeployJob(groupID);
                            break;
                        case "azure":
                            await createAzureDeploy();
                            break;
                    }
            }
            emit("close");
        },
        { loading: true });
}

async function createAzureDeploy() {
    const deployItems: Array<IExportItem> = [];
    const itemIdObj: Record<string, { item: IModel, path: string }> = {};

    viewModelManager.flatMapItems(viewModel.studio.items, (item, path) => {
        if (item.objectType != "model") { return; }
        itemIdObj[item.ID] = { item, path };
        return item;
    });

    const traverseSelectedItems = (items?: Array<ITableData>) => {
        if (!items || items.length == 0) { return; }
        items.forEach(item => {
            if (item.type == "module") {
                traverseSelectedItems(item.children);
                return;
            }

            const itemMap = itemIdObj[item.id];

            const deployItem: IExportItem = {
                id: item.id,
                name: jobUtils.getFileFullname(itemMap.item),
                path: itemMap.path,
                type: jobUtils.getItemType(itemMap.item),
                ver: item.version || "latest",
                isRootPage: itemMap.item.isRootPage,
                ownerType: 'application',
                ownerName: viewModel.studio.appName,
                size: itemMap.item.size,
            };
            deployItems.push(deployItem)
        });
    };

    traverseSelectedItems(items.value);

    await publish.createUI(deployItems, props.platformSelection);
}

async function initHistory(groupID: string) {
    return qcloudApi.initPublishHistory({
        appID: viewModel.studio.appId,
        ID: groupID,
        isMultiStep: true,
        type: props.screenType == "Deploy" ? "publish" : "download"
    });
}

function isUsingPlateau() {
    const applications = viewModel.user!.selectedOrganization!.applications;

    if (!applications)
        return false;

    if (!applications.entity && !applications.process && !applications.bpmn)
        return false;

    return true;
}

function getItemPath(asset: ITableData, ownerName: string) {
    if (isUsingPlateau())
        return "";
    if (asset.type == "qjson")
        return ownerName;
    return "";
}

function isUISettings(asset: ITableData) {
    return asset.name == "UI Settings";
}


async function packJob(groupID: string) {
    let exportItemMap = new Map<string, Array<IExportItem>>();

    items.value.forEach((asset) => {
        if (asset.checked) {
            //QUI
            if (asset.type == 'qjson') {
                const isThisrootPage = viewModel.studio.items.find(i => i.objectType == "model" && i.modelType == "qjson" && (i.additionals as IQJsonAdditionals).isRootPage && i.ID == asset.id);

                const obj: IExportItem = {
                    id: asset.id,
                    ver: asset.version!,
                    type: jobUtils.getItemType({ usageType: asset.usageType, type: asset.type } as IExportViewItem),
                    path: "",
                    name: jobUtils.getFileFullname({ usageType: asset.usageType, type: asset.type, name: asset.name } as IExportViewItem),
                    ownerType: 'application',
                    ownerName: viewModel.studio.appName,
                    isRootPage: isThisrootPage ? true : undefined,
                };

                let applicationModels = exportItemMap.get(viewModel.studio.appName);
                if (applicationModels && applicationModels.length > 0) {
                    applicationModels.push(obj);
                    exportItemMap.set(viewModel.studio.appName, applicationModels);
                }
                else
                    exportItemMap.set(viewModel.studio.appName, [obj]);
                return;
            }
        }
        if (asset.anyChildChecked && asset.children) {
            let checkedItems = Array<IExportItem>();
            asset.children!.forEach(child => {
                if (!child.checked || child.type == "module")
                    return;
                const exportItem: IExportItem = {
                    id: child.id,
                    ver: child.version!,
                    type: jobUtils.getItemType({ usageType: child.usageType, type: child.type, name: child.name } as IExportViewItem),
                    path: !isUISettings(asset) ? getItemPath(child, asset.name) : "",
                    name: jobUtils.getFileFullname({ usageType: child.usageType, type: child.type, name: child.name } as IExportViewItem),
                    ownerType: isUISettings(asset) || !isUsingPlateau() ? "application" : (asset.type === 'module' ? 'module' : 'application'),
                    ownerName: isUISettings(asset) || !isUsingPlateau() ? viewModel.studio.appName : asset.name
                };
                if (exportItem.type == "entityDesigner")
                    exportItem.entityGenerationDomain = viewModel.user!.selectedOrganization!.actions?.entityGenerationDomain;
                checkedItems.push(exportItem);
            });

            if (checkedItems.length > 0) {
                if (!isUISettings(asset) && isUsingPlateau())
                    exportItemMap.set(asset.name, checkedItems);
                else {
                    let applicationModels = exportItemMap.get(viewModel.studio.appName);
                    if (applicationModels && applicationModels.length > 0) {
                        applicationModels.forEach(e => checkedItems.push(e));
                    }
                    exportItemMap.set(viewModel.studio.appName, checkedItems);
                }
            }

        }
    })

    let jobs = Array<ICreateExportJobParam>();
    exportItemMap.forEach((v, k) => {
        jobs.push({
            appID: viewModel.studio.appId,
            exportType: "qui",
            selectionData: v,
            layer: 0,
            groupID: groupID
        })
    })

    await packJobsUseCase.createPackJobs(jobs);
}

async function createExportJob(groupID: string) {
    await executor.executeUseCase<ICreateExportJob>(ICreateExportJob, {
        appID: viewModel.studio.appId,
        exportType: "qui",
        selectionData: [],
        layer: 1,
        groupID: groupID
    });
}

async function createDeployJob(groupID: string) {
    //1-(n) L1- deployJob(build)
    let packages = await qcloudApi.getExportJobsByGroupId({ groupID })
        .then(jobs => jobs.jobs.filter(e => !isExternalModule(e.jobData as IExportJobData)).map(e => {
            const jobData = e.jobData as IExportJobData;
            return {
                ownerName: jobData.items[0].ownerName,
                jobID: e.jobID,
                type: jobData.items[0].ownerType,
                mainDir: getDeployableArchiveRoot(jobData)
            }
        }));

    if (!packages || packages.length <= 0)
        return;

    let buildJobs = Array<ICreateDeployJobParam>();
    packages.forEach(pack => {
        buildJobs.push({
            appID: viewModel.studio.appId,
            type: "build",
            package: {
                type: pack.type as DeployType,
                name: pack.ownerName!,
                mainDir: pack.mainDir!,
                zip: `${pack.jobID}.zip`,
            },
            layer: 1,
            groupID: groupID
        })
    })

    await deployJobsUseCase.createBuildJobs(buildJobs);

    //2-(1) L2- deployJob(deployment)
    let jobParam: ICreateDeployJobParam = {
        appID: viewModel.studio.appId,
        type: "deployment",
        deployConfig: {
            process: {
                configModelIDs: getProcessConfigList(),
                enabled: items.value.filter(asset => asset.checked && asset.children && asset.children.filter(child => child.checked && child.type == "bpmn")).length > 0
            }
        },
        layer: 2,
        groupID: groupID
    }

    await deployJobsUseCase.startDeploymentJob(jobParam);
}

function isExternalModule(jobData: IExportJobData){
    //FIXME: external modules may be dynamic
    return jobData.items[0].ownerType == "module" && jobData.items[0].ownerName == "processmanager";
}

function getProcessConfigList() {
    let processConfigList = Array<IProcessConfigModelItem>();
    items.value.forEach((asset) => {
        if (asset.checked && asset.children) {
            const usingService = asset.children.find(c => c.type == "entityDesigner");
            if (!usingService)
                return;

            const serviceName = PlateauHelper.getServiceNameFromModel(usingService.name);
            asset.children.forEach(child => {
                if (child.checked && child.type == "process") {
                    processConfigList.push({
                        moduleName: serviceName,
                        modelId: child.id,
                        modelName: child.name
                    })
                }
            })
        }
    })
    return processConfigList;
}

function getDeployableArchiveRoot(jobData: IExportJobData) {
    //FIXME: change external modules
    if(jobData.items[0].ownerName == "processmanager")
        return "";
    if (jobData.items[0].ownerType == "module") {
        let edModel = jobData.items.find(e => e.type == "entityDesigner");
        if (!edModel)
            throw new Error("!!! this module cannot deploy. Because its have not a service");
        return PlateauHelper.getServiceNameFromModel(edModel.name);
    }
    return PlateauHelper.getQuiPackageNameFromAppName(jobData.app.name);
}

function cloudTargetMap(name: string, pureVal : boolean = false) {
    const map: Record<keyof IOrganizationCloud, string> = { azure: "Public Cloud", plateauGCP: "Plateau Platform Playground"};
    if(pureVal) {
        return Object.keys(map).find(e => {
            if(map[e as keyof IOrganizationCloud] === name){
                return true;
            }
        })
    }else {
        let key = Object.keys(map).includes(name);
        if(!key)
            return;
        return map[name as keyof IOrganizationCloud];
    }
}

function onTargetSelect(value: string) {
    selectedTarget.value = value
}


