
export class PlateauHelper {
    public static purify = (str: string) => str.replace(/[^a-zA-Z0-9]/g, "").toLowerCase();

    public static getQuiPackageNameFromAppName = (appName: string) => `${PlateauHelper.purify(appName)}-qui`;

    public static getApplicationUINameFromQuiPkg = (quiPackageName: string) => PlateauHelper.purify(quiPackageName.replace("-qui", "ui"));
    
    public static getApplicationHostNamePrefix = (appName: string) => `${PlateauHelper.purify(appName)}ui`;

    public static getServiceNameFromModel = (modelName: string) => PlateauHelper.purify(modelName.replace(".dm", ""));
}